import React from 'react';

const services = [
  {
    title: 'Mecnico',
    description: 'Mantenimiento de maquinaria mecnica, incluyendo inspeccin, reparacin y actualizacin de equipos industriales. Ofrecemos soluciones preventivas para evitar fallos en la maquinaria.',
    img: '/mecanico.jpg',
    icon: '/mecicon.jpg'
  },
  {
    title: 'Hidrulico',
    description: 'Soluciones completas en sistemas hidrulicos, desde la instalacin hasta la reparacin. Nos especializamos en el diagnstico de problemas y optimizacin del rendimiento.',
    img: '/hidraulico.jpg',
    icon: '/hidricon.jpg'
  },
  {
    title: 'Elctrico',
    description: 'Instalacin y mantenimiento de sistemas elctricos industriales y residenciales. Nuestros servicios incluyen desde la instalacin de cableado hasta la reparacin de circuitos complejos.',
    img: '/panel_electrico.jpg',
    icon: '/elecicon.jpg'
  },
  {
    title: 'Generadores y Compresores',
    description: 'Mantenimiento y reparacin de generadores y compresores de diversas marcas. Proporcionamos asistencia tcnica para garantizar el correcto funcionamiento y la mxima eficiencia de tus equipos.',
    img: '/generador.jpg',
    icon: '/geneicon.jpg'
  },
  {
    title: 'Automatizacin',
    description: 'Soluciones avanzadas en automatizacin industrial para optimizar los procesos de produccin y mejorar la eficiencia. Desde la programacin de PLC hasta la instalacin de sistemas SCADA.',
    img: '/PLC.jpg',
    icon: '/plcicon.jpg'
  },
  {
    title: 'Climatizacin',
    description: 'Instalacin y mantenimiento de sistemas de climatizacin para ambientes industriales y comerciales. Garantizamos confort y eficiencia energtica en tus instalaciones.',
    img: '/chiller.jpg',
    icon: '/chillericon.webp'
  },
];

const Services = () => {
  return (
    <section id="services" className="py-16 bg-gray-100 scroll-mt-20">
      <h2 className="text-center text-4xl font-bold text-blue-800">Nuestros Servicios</h2>
      <p className="text-center mt-4 text-gray-600 max-w-2xl mx-auto">Ofrecemos una variedad de servicios especializados diseados para satisfacer las necesidades de diferentes sectores industriales. Nuestro equipo de expertos est preparado para brindarte soluciones personalizadas.</p>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 mt-8">
        {services.map((service, index) => (
          <div key={index} className="bg-white shadow-lg rounded-lg overflow-hidden transition-transform transform hover:scale-105">
            <img src={service.img} alt={service.title} className="w-full h-48 object-cover"/>
            <div className="p-6 flex flex-col items-center">
              <img src={service.icon} alt={`${service.title} icon`} className="w-12 h-12 mb-4"/>
              <h3 className="text-2xl font-bold text-blue-800">{service.title}</h3>
              <p className="mt-4 text-gray-600 text-justify">{service.description}</p>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Services;
